
.unActiveDialog {
    border: 2px solid #1976d2;
    border-bottom: none;
    display:block;
    text-decoration: none;
    min-height: 60px;
    padding:15px 10px 5px 12px;
    word-break: break-all;
    max-width: 100%;
    border-radius: 6px;
    font-size: 18px;
    font-weight: 600;
    color: #43505c;
    display:flex;
    align-items: center;
    &:hover {
        background-color: #99dfffad;
    }
}
.activeDialog {
    display:block;
    display:flex;
    border: 2px solid #1976d2;
    border-bottom: none;
    font-weight: 600;
    text-decoration: none;
    min-height: 60px;
    padding:15px 0 5px 12px;
    align-items: center;
    max-width: 100%;
    word-break: break-all;
    border-radius: 6px;
    background-color: #1976d2;
    font-size: 18px;
    color: #fff;
}
    

.avatar {
    width: 44px;
    border-radius: 50%;
    margin-right: 5px;
}




.removeButton {
    box-sizing: border-box;
    cursor: pointer;
    color: rgba(238, 6, 37, 0.533);
    max-height: 27px;
    margin: 0 10px 0 0;
    padding: 5px 5px;
    background-color: rgba(245, 177, 186, 0.533);
    border-radius: 7px;
}


.boxList {
    width: 100%;
    
}

.list {
    list-style: none;
    margin: 0;
    padding: 0;
    li:last-child {
        border-bottom: 2px solid #1976d2;
        border-radius: 6px;
    }
}
